<script>
    import Vue from 'vue';

    export default {
        props: {
            showCloseButton: Boolean
        },
        data() {
            return {
                x: 0,
                y: 0,
                height: 0,
                visible: false
            }
        },
        methods: {
            open() {
                this.visible = true;
                Vue.nextTick(() => {
                    this.align();
                    setTimeout(() => {
                        this.$emit('ready');
                    }, 200)
                })
            },
            close() {
                this.visible = false;
            },
            align() {
                let bbox = this.$refs.box.getBoundingClientRect();
                let bgbbox = this.$refs.bg.getBoundingClientRect();

                this.x = bgbbox.width / 2 - bbox.width / 2;
                this.y = 10;
                this.height = bgbbox.height - 60;
            }
        }
    }
</script>

<template>
    <div class="uimodalbox" v-if="visible" transition="appear">
        <div ref="bg" class="uimodalbg white" @mousedown="close"></div>
        <div ref="box" class="uimodal shadow" :style="{ left: `${x}px`, top: `${y}px` }">
            <div class="uimodalclose" v-show="showCloseButton" @click="close">
                <span class="smicon cross"></span>
            </div>
            <div class="uimodalbody" :style="{ 'max-height': `${ height }px` }" }>
                <slot></slot>
            </div>
        </div>
    </div>
</template>
