import Vue from 'vue';
import VueRouter from 'vue-router';
import Tile from './tile.vue';
import AppTable from './app-table.vue';
import Dashboard from './dashboard.vue';
import CleverForms from './apps/clever-forms.vue';
import MyPay from './apps/mypay.vue';
import Modal from './modal.vue';
import NavBar from './navbar.vue';
import Ajax from '../../dashboard-common/vue/ajax';
import VeeValidate from 'vee-validate';
import {
    Validator
} from 'vee-validate';

Vue.use(Ajax, {
    handleUnautorized: true
});

Vue.use(VueRouter);
Vue.use(VeeValidate);

const reservedLinks = [
    '/Profile/Security',
    '/Profile/Approvals',
    '/Profile/PeopleStreme',
    '/CleverForms/Inbox',
    '/CleverForms/Template',
    '/CleverForms/Admin'
];

const reservedNames = [
    'MyDocuments',
    'BankAccounts',
    'StaffSearch',
    'Leave',
    'MyPay',
    'MyAccount',
    'WorkSchedules',
    'CleverForms',
    'Timesheets'
];

Validator.localize('en', {
    messages: {
        url: () => 'Please enter the full URL',
        required: () => 'This field is required',
        numeric: () => 'The field may only contain numeric characters'
    }
});

Validator.extend('url', {
    validate(value) {
        var regex = /^(http|https):\/\/+[\www\d]+\.[\w]+(\/[\w\d]+)?/;
        return regex.test(value);
    }
});

Validator.extend('reservedLinks', {
    validate(value) {
        var path = new URL(value).pathname;
        return reservedLinks.includes(path) ? false : true;
    },
    getMessage: () => 'Sorry, you can\'t use that URL'
});

Validator.extend('reservedNames', {
    validate(value) {
        return reservedNames.includes(value) ? false : true;
    },
    getMessage: () => 'Sorry, you can\'t use that name'
});

Vue.component('tile', Tile);
Vue.component('modal', Modal);

new Vue({
    el: '#app',
    data() {
        return {
            loading: true,
            dashboardConfig: {},
            loader: document.config.loaders.dark,
            showNav: false,
            hideNav: false,
            rank: 0
        }
    },
    methods: {
        getDashboardConfig() {
            this.loading = true;
            this.$ajax.get('/Manage/GetConfig')
                .then(resp => {
                    this.dashboardConfig = resp.data;
                    this.dashboardConfig.Apps.filter(app => {
                        app.Link.Menu.forEach(function (menu) {
                            var show = true;
                            try {
                                var path = new URL(menu.Url).pathname;
                                show = reservedLinks.includes(path) ? false : true;
                            } catch (e) { }

                            menu.Show = show;
                        });
                        app.ShowName = reservedNames.includes(app.Name) ? false : true;
                        app.Valid = true;
                    });
                    this.rank = Math.max(...this.dashboardConfig.Apps.map(app => app.Rank)) + 1;
                })
                .catch(() => { })
                .then(() => {
                    this.loading = false;
                });
        },
        onNavToggle(mobile) {
            if (mobile) this.showNav = !this.showNav;
            else this.hideNav = !this.hideNav;
        },
        onNavHide() {
            this.showNav = false;
        }
    },
    mounted() {
        this.getDashboardConfig();
    },
    components: {
        NavBar
    },
    router: new VueRouter({
        routes: [{
            path: '/Dashboard',
            component: Dashboard
        },
        {
            path: '/CleverForms',
            component: CleverForms
        },
        {
            path: '/MyPay',
            component: MyPay
        },
        {
            path: '*',
            component: AppTable
        }
        ],
        linkActiveClass: 'selected'
    })
})