<script>
    export default {
        data() {
            return {
                apps: []
            }
        },
        created() {
            this.$root.$watch('dashboardConfig', (config) => {
                this.apps = config.Apps;
            });
        },
        mounted() {
            let config = this.$root.dashboardConfig;
            this.apps = config ? config.Apps : [];
        },
        computed: {
            visibleApps() {
                let visibleApps = this.apps;
                if (this.apps) {
                    visibleApps = this.apps.filter(app => {
                        return app.Name != 'StaffSearch'
                    })
                }
                return visibleApps;
            }
        }
    }
</script>

<template>
    <div>
        <div class="app-table" v-show="!loading">
            <router-link :to="{ path: '/Dashboard' }">
                <span>
                    <span class="icon icon-gauge"></span>
                    <div class="label">Dashboard</div>
                </span>
            </router-link>
            <router-link v-for="(app, index) in visibleApps" :to="{ path: '/' + app.Name }" :key="index">
                <span>
                    <span :class="'icon icon-' + app.Link.Icon"></span>
                    <div class="label">{{ app.Name }}</div>
                </span>
            </router-link>
        </div>
    </div>
</template>
