﻿<script>
export default {
  data() {
    return {
      config: {},
      currentApp: null
    };
  },
  created() {
    this.$root.$watch("dashboardConfig", config => {
      this.config = config;
    });
  },
  mounted() {
    this.config = this.$root.dashboardConfig;
    this.selectApp();
  },
  watch: {
    config() {
      this.selectApp();
    },
    currentApp: {
      handler: async function(value) {
        await this.$nextTick();
        value.Valid = await this.$validator.validateAll();
        this.config = {};
        this.config = this.$root.dashboardConfig;
      },
      deep: true
    }
  },
  methods: {
    selectApp(app) {
      if (app) this.currentApp = app;
      else if (!app && !this.currentApp && this.config.Apps)
        this.currentApp = this.config.Apps[0];
      if (this.currentApp && !this.currentApp.Link) this.currentApp.Link = {};
    },
    async saveConfig() {
      let valid = await this.$validator.validateAll();
      if (valid) {
        this.$root.loading = true;
        this.$ajax
          .post("/Manage/SaveConfig", this.config)
          .then(resp => {
            this.config.Id = resp.data;
          })
          .catch(() => {})
          .then(() => {
            this.$root.getDashboardConfig();
            this.$root.loading = false;
          });
      }
    },
    addAppMenuItem() {
      this.currentApp.Link.Menu.push(this.defaultLinkValues());
    },
    defaultLinkValues() {
      return {
        Enabled: true,
        External: true,
        Icon: "user-check",
        Menu: [],
        Name: "",
        PageId: 0,
        Text: "",
        Url: "",
        Show: true
      };
    },
    addApp() {
      let app = {
        Id: "",
        Link: this.defaultLinkValues(),
        Name: "New App " + this.$root.rank,
        Widgets: [],
        ShowName: true,
        Valid: true,
        Rank: this.$root.rank
      };

      this.config.Apps.push(app);
      this.selectApp(app);
      this.$root.rank++;
    },
    removeAppMenuItem(item) {
      let menu = this.currentApp.Link.Menu;
      let index = menu.indexOf(item);
      menu.splice(index, 1);
    },
    removeApp(app) {
      let index = this.config.Apps.indexOf(app);
      this.config.Apps.splice(index, 1);
      this.currentApp = this.config.Apps[0];
    },
    disableSaveBtn() {
      return (
        this.config.Apps &&
        this.config.Apps.some(app => {
          return !app.Valid;
        })
      );
    }
  }
};
</script>

<template>
  <div>
    <tile title="Dashboard Configuration" icon="icon-wrench" class="dashboard-config-tile w-100">
      <div class="section">
        <div class="section-body">
          <div class="default-form">
            <div class="form-row">
              <label>Host URL:</label>
              <input type="text" disabled v-model="config.Url">
            </div>
            <div class="form-row">
              <label>Enabled</label>
              <input type="checkbox" v-model="config.Enabled">
            </div>
            <hr>
            <div class="title-row section-title">Applications</div>
            <div class="apps-config-box form-row">
              <div class="app-list">
                <ul>
                  <li
                    v-for="(app, index) of config.Apps"
                    :class="{ selected: currentApp && currentApp.Name == app.Name, error: !app.Valid }"
                    @click="selectApp(app)"
                    :key="index"
                  >
                    <span :class="'icon icon-' + app.Link.Icon"></span>
                    <span>{{ app.Name }}</span>
                  </li>
                </ul>
                <div class="btn-add config-item" @click="addApp">
                  <span class="icon-plus"></span>
                  <span class="add-text">Add Application</span>
                </div>
              </div>
              <div class="app-config" v-if="currentApp" :key="currentApp">
                <div v-if="currentApp.ShowName">
                  <span class="btn-rem-app icon-trash" @click="removeApp(currentApp)"></span>
                  <div class="title-row section-title">Application</div>
                  <br>
                  <div class="form-row">
                    <label>Name</label>
                    <input
                      type="text"
                      name="appName"
                      v-validate="'required|reservedNames'"
                      v-model="currentApp.Name"
                    >
                    <div v-if="errors.has('appName')" class="error">{{ errors.first('appName') }}</div>
                  </div>
                </div>
                <div class="title-row section-title">Link</div>
                <br>
                <div class="form-row">
                  <label>Enabled</label>
                  <input type="checkbox" v-model="currentApp.Link.Enabled">
                </div>
                <div class="form-row">
                  <label>External</label>
                  <input type="checkbox" v-model="currentApp.Link.External">
                </div>
                <div class="form-row">
                  <label>Link Text:</label>
                  <input
                    type="text"
                    name="linkText"
                    v-validate="'required'"
                    v-model="currentApp.Link.Text"
                  >
                  <div v-if="errors.has('linkText')" class="error">{{ errors.first('linkText') }}</div>
                </div>
                <div class="form-row">
                  <label>Link Icon:</label>
                  <input
                    type="text"
                    name="linkIcon"
                    v-validate="'required'"
                    v-model="currentApp.Link.Icon"
                  >
                  <div v-if="errors.has('linkIcon')" class="error">{{ errors.first('linkIcon') }}</div>
                </div>
                <div class="form-row">
                  <label>Link URL:</label>
                  <input
                    type="text"
                    name="linkUrl"
                    v-validate="'required|url|reservedLinks'"
                    v-model="currentApp.Link.Url"
                  >
                  <div v-if="errors.has('linkUrl')" class="error">{{ errors.first('linkUrl') }}</div>
                </div>
                <div class="form-row">
                  <label>ESS Page ID:</label>
                  <input
                    type="text"
                    name="pageId"
                    v-validate="'required|numeric'"
                    v-model="currentApp.Link.PageId"
                  >
                  <div v-if="errors.has('pageId')" class="error">{{ errors.first('pageId') }}</div>
                </div>
                <div v-if="currentApp.Widgets && currentApp.Widgets.length > 0">
                  <hr>
                  <div class="title-row section-title">Widgets</div>
                  <br>
                  <div></div>
                  <div v-for="(w, index) of currentApp.Widgets" class="form-row" :key="index">
                    <label>
                      <input type="checkbox" v-model="w.Enabled">
                      {{ w.Label }}
                    </label>
                  </div>
                </div>
                <div>
                  <hr>
                  <div class="title-row section-title">Menu</div>
                  <br>
                  <div
                    class="link-menu-item"
                    v-for="(m, index) in currentApp.Link.Menu"
                    :key="index"
                  >
                    <span class="btn-rem icon-trash" v-if="m.Show" @click="removeAppMenuItem(m)"></span>
                    <div class="form-row">
                      <label>{{ m.Name ? `Enable ${m.Name}` : 'Enabled' }}</label>
                      <input type="checkbox" v-model="m.Enabled">
                    </div>
                    <div class="form-row">
                      <label>External</label>
                      <input type="checkbox" v-model="m.External">
                    </div>
                    <div class="form-row">
                      <label>Link text:</label>
                      <input
                        type="text"
                        :name="'text-' + index"
                        v-validate="'required'"
                        v-model="m.Text"
                      >
                      <div
                        v-if="errors.has('text-' + index)"
                        class="error"
                      >{{ errors.first('text-' + index) }}</div>
                    </div>
                    <div class="form-row">
                      <label>Link icon:</label>
                      <input
                        type="text"
                        :name="'icon-' + index"
                        v-validate="'required'"
                        v-model="m.Icon"
                      >
                      <div
                        v-if="errors.has('icon-' + index)"
                        class="error"
                      >{{ errors.first('icon-' + index) }}</div>
                    </div>
                    <div class="form-row" v-if="m.Show">
                      <label>Link URL:</label>
                      <input
                        type="text"
                        v-validate="'required|url|reservedLinks'"
                        :name="'linkUrl-' + index"
                        v-model="m.Url"
                      >
                      <div
                        v-if="errors.has('linkUrl-' + index)"
                        class="error"
                      >{{ errors.first('linkUrl-' + index) }}</div>
                    </div>
                    <div class="form-row">
                      <label>ESS Page ID:</label>
                      <input
                        type="text"
                        :name="'pageId-' + index"
                        v-validate="'required|numeric'"
                        v-model="m.PageId"
                      >
                      <div
                        v-if="errors.has('pageId-' + index)"
                        class="error"
                      >{{ errors.first('pageId-' + index) }}</div>
                    </div>
                  </div>
                  <div class="btn-add link-menu-item" @click="addAppMenuItem">
                    <span class="icon-plus"></span>
                    <span class="add-text">Add new menu item</span>
                  </div>
                </div>
              </div>
            </div>
            <div class="form-row row-bottom">
              <button
                class="button green"
                @click.prevent="saveConfig"
                :class="{'disabled': disableSaveBtn()}"
              >
                <span class="b-icon icon-save"></span>
                <span>Save</span>
              </button>
            </div>
          </div>
        </div>
      </div>
    </tile>
  </div>
</template>